html {
  background-color: #FFFFFF;
  font-size: 16px;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  min-width: 300px;
  overflow-x: hidden;
  overflow-y: scroll;
  text-rendering: optimizeLegibility;
  text-size-adjust: 100%;
}

[v-cloak] {
  display: none;
}

article,
aside,
figure,
footer,
header,
hgroup,
section {
  display: block;
}

body,
button,
input,
optgroup,
select,
textarea {
  font-family: "Roboto", "Open Sans", sans-serif;
}

body {
  color: #000000;
  font-size: 1em;
  font-weight: normal;
  line-height: 1.5;
}

a {
  border-bottom: 1px solid #000000;
  color: #000000;
  cursor: pointer;
  letter-spacing: 0.1em;
  padding-bottom: 0.15em;
  text-decoration: none;
  text-transform: uppercase;
  transition: color 0.25s ease-in;
}
a:hover {
  color: #1a1a1a;
  transition: color 0.2s ease-out;
}

main {
  display: flex;
  flex-wrap: wrap;
}

img {
  height: auto;
  max-width: 100%;
}

input[type=checkbox],
input[type=radio] {
  vertical-align: baseline;
}

small {
  font-size: 0.875em;
}

span {
  font-style: inherit;
  font-weight: inherit;
}

fieldset {
  border: none;
}

table,
td,
th {
  vertical-align: top;
}
table:not([align]),
td:not([align]),
th:not([align]) {
  text-align: inherit;
}

@media only screen and (min-width: 768px) and (orientation: landscape) {
  main {
    height: 100vh;
  }
}